import React, { Component } from 'react';
import { connect } from 'react-redux';
import MainLogin from 'components/Main/Login';
import WithLogged from 'lib/hoc/WithLogged';
import { Actions } from 'store/actionCreators';
import { storage, keys } from 'lib/library';
import * as utils from 'utils/auth';

class LoginContainer extends Component {
  state={
    email: "",
    password: "",
    remember:false,
  }
  handleSubmit = (e) => {
    e.preventDefault()
    const { email, password, remember } = this.state;
    if (remember) {
      storage.set(`remember`, true);
      storage.set(`email`, email);
    } else {
      storage.clear()
    }

    if(!utils.regEmail(email)){
      alert('Please check your email format.');
      return false;
    }
    if(!utils.regPassword(password)){
      alert('Please check your Password format.');
      return false;
    }
    Actions.login({ email, password });
  }

  handleChange = (e) => {
    const [{ state }, { name, value }] = [this, e.target];
    this.setState({
      [name]: name === 'remember' ? !state.remember : value
    });
  }
  componentDidMount() {
    storage.set(keys);
    const storageRemember = storage.get(`remember`);
    const storageEmail = storage.get(`email`);
    this.setState({
      email:storageEmail,
      remember:storageRemember
    })
  }
  render() {
    const {email,password,remember} = this.state;
    const {logged,pending,response} = this.props;
    const loginResponse ={
      ['0']:()=>null,
      ['1']:()=>{
        storage.set(`${keys}logged`, true);
        alert('Welcome.');
      },
      ['2']:()=>{alert('Please check your Email and Password.')},
      ['3']:()=>{alert('User not found Please register.')},
      ['4']:()=>{alert('Server error.')},
    };
    loginResponse[response]();
    Actions.login_alert();
    return (
      <div>
        <WithLogged url="/dashboard" isLogged />
        <MainLogin
          remember={remember}
          email={email}
          password={password}
          logged={logged}
          pending={pending}
          response={response}
          handleLogin={this.handleLogin}
          handleChange={this.handleChange}
          handleSubmit={this.handleSubmit}
        />
      </div>
    );
  }
}
export default connect(
  ({ auth }) => ({
    logged: auth.logged,
    pending: auth.pending,
    response: auth.response
  })
)(LoginContainer);