import {createAction} from 'redux-actions';

// action
export const LOGIN_LOGGED = 'login/LOGGED';

export const WINDOW_PAGE = 'window/PAGE';

export const DASHBOARD_TITLE = 'dashboard/TITLE';

// dispatch
export const login_logged = createAction(LOGIN_LOGGED);

export const window_page = createAction(WINDOW_PAGE);

export const dashboard_title = createAction(DASHBOARD_TITLE);


// saga
export const LOGIN         = 'login/LOGIN';
export const LOGIN_PENDING = 'login/LOGIN_PENDING';
export const LOGIN_SUCCESS = 'login/LOGIN_SUCCESS';
export const LOGIN_FAILURE = 'login/LOGIN_FAILURE';
export const LOGIN_LOGOUT  = 'login/LOGIN_LOGOUT';
export const LOGIN_ALERT   = 'login/LOGIN_ALERT';

export const login         = createAction(LOGIN);
export const login_pending = createAction(LOGIN_PENDING);
export const login_success = createAction(LOGIN_SUCCESS);
export const login_failure = createAction(LOGIN_FAILURE);
export const login_logout  = createAction(LOGIN_LOGOUT);
export const login_alert   = createAction(LOGIN_ALERT);