import {handleActions} from 'redux-actions';
import produce from 'immer';
import * as actions from 'store/actions';


let initialState ={
  pending:false,
  logged:false,
  response:0,
  alert:0,
}


export default handleActions({
  [actions.LOGIN_LOGGED]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('hello logged');
      draft.logged = diff;
      draft.pending = false;
      draft.response = 1;
    })
  },
  [actions.LOGIN_PENDING]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('LOGIN_PENDING');
      draft.pending = true;
      draft.response = 0;
    })
  },
  [actions.LOGIN_SUCCESS]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('LOGIN_SUCCESS');
      draft.logged = true;
      draft.pending = false;
      draft.response = 1;
    })
  },
  [actions.LOGIN_FAILURE]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('LOGIN_FAILURE');
      draft.logged = false;
      draft.pending = false;
      draft.response = 2;
    })
  },
  [actions.LOGIN_LOGOUT]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('LOGIN_LOGOUT');
      draft.logged = false;
      draft.pending = false;
      draft.response = 0;
    })
  },
  [actions.LOGIN_ALERT]:(state,{payload:diff})=>{
    return produce(state,draft=>{
      console.log('LOGIN_ALERT');
      draft.response = 0;
    })
  },
},initialState )




