import {all,fork,takeEvery,call} from 'redux-saga/effects';
import {Actions} from 'store/actionCreators';
import * as actions from 'store/actions';
import * as API  from 'lib/api/login';


function* handleAuthorize({payload:diff}){
  console.log(`Actions.login`);
  Actions.login_pending();
  const {data,error} = yield call(API.postLogin,diff);
  if(data && !error && data.result === 1){
    Actions.login_success()
  }else{
    Actions.login_failure()
  }
}

export default function* RootSaga(){
  yield all([
    takeEvery(actions.LOGIN,handleAuthorize)
  ])
}


